 *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#canvas {
    display: block;

    position: relative;
    left: 80px;
    top: 100px;

}
button{
    margin: 0;
    padding: 0;
    border: 1px solid transparent;
     outline: none;
    background: transparent;

}
.icon {
    width: 32px; height: 32px;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}
.main{

}
.other{
  position: relative;
   background: red;
    top: 18px;
}
.color{
    position: absolute;
    left: 10px;
    top: -600px;
    padding: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 300px;


}
.shape{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    padding-top: 10px;


}
.yellow{
    background: yellow;
    margin-bottom: 2px;
}
.black{
    background: black;
    margin-bottom: 2px;
}
.green{
    background: green;
    margin-bottom: 2px;
}
.pink{
    background: pink;
    margin-bottom: 2px;
}
.red{
    background: red;
    margin-bottom: 2px;
}
.brush{
    width: 300px;
    height: 30px;
    position: absolute;
     top: -700px;
    left: 0px;
    display: flex;
    justify-content: center;
    align-content: center;

}


#pen{

    margin-left: 6px;
}
#era{
    margin-left: 6px;
 }
#clear{
    margin-left: 6px;
}
#pen_big{
     height: 32px;
     width: 32px;
    border: 2px solid #cccccc;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 6px;
}
.big{

    height: 28px;
    width: 10px;
    background: black;


}
#pen_small{
    height: 32px;
    width: 32px;
    border: 2px solid #cccccc;
    display: flex;
    justify-content: center;
    align-content: center;
    margin-left: 6px;
    align-items: center;

}
.small{
    height: 28px;
    width: 6px;
    background: black;
}

@media (max-width:500px) {
    .other{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .other .brush{
        top: -60vmax;
        /*margin-top: 30px;*/
        left: 3vmax;
    }
    .other .color{
        flex-direction: row;
        top:  -74vmax;
        left: 11vmax;
    }
    .yellow{
        margin: 6px;
    }
    .pink{
        margin: 6px;
    }
    #canvas{
        /*max-width: 120vmax;*/
        width:50vmax ;
        max-height: 60vmax;
       border-left:none ;
        border-right: none;
        top: 120px;
        left: 3vmax;

    }

}






